// THREE.JS 
const scene = new THREE.Scene();

const camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);

const renderer = new THREE.WebGLRenderer({
  canvas: document.querySelector('#bg'),
});

renderer.setPixelRatio(window.devicePixelRatio);
renderer.setSize(window.innerWidth, window.innerHeight);
camera.position.setZ(25);
camera.position.setX(0);
camera.position.setY(0);

console.log("X : "+camera.position.x);
console.log("Y : "+camera.position.y);
console.log("Z : "+camera.position.z);

function addStar() {
  const x = 0, y = 0;

const heartShape = new THREE.Shape();

heartShape.moveTo( x + 2, y + 2 );
heartShape.bezierCurveTo( x + 2.5, y + 2.5, x + 2, y, x, y );
heartShape.bezierCurveTo( x - 3, y, x - 3, y + 3.5,x - 3, y + 3.5 );
heartShape.bezierCurveTo( x - 3, y + 5.5, x - 1.5, y + 7.7, x + 2.5, y + 9.5 );
heartShape.bezierCurveTo( x + 6, y + 7.7, x + 8, y + 5.5, x + 8, y + 3.5 );
heartShape.bezierCurveTo( x + 8, y + 3.5, x + 8, y, x + 5, y );
heartShape.bezierCurveTo( x + 3.5, y, x + 2.5, y + 2.5, x + 2.5, y + 2.5 );

const geometry = new THREE.ShapeGeometry( heartShape );
const material = new THREE.MeshBasicMaterial( { color: 0xAFD7FF } );
const star = new THREE.Mesh( geometry, material ) ;

  const [x1, y1, z1] = Array(3)
    .fill()
    .map(() => THREE.MathUtils.randFloatSpread(800));

  star.position.set(x1, y1, z1);
  scene.add(star);
}

Array(200).fill().forEach(addStar);


// Torus
const geometry = new THREE.TorusGeometry(6, 0.7, 16, 100);
const material = new THREE.MeshStandardMaterial({ color: 0x1e90ff });
const torus = new THREE.Mesh(geometry, material);
torus.position.z = 10;
torus.position.setX(15);
torus.position.setY(0);
scene.add(torus);

// Lights
const pointLight = new THREE.PointLight(0xffffff);
pointLight.position.set(5, 5, 5);
const ambientLight = new THREE.AmbientLight(0xffffff);
scene.add(pointLight, ambientLight);

renderer.render(scene, camera);

// Moon
const moonTexture = new THREE.TextureLoader().load('coding.png');
const normalTexture = new THREE.TextureLoader().load('brick.jpg');

const moon = new THREE.Mesh(
  new THREE.SphereGeometry(5, 32, 32),
  new THREE.MeshStandardMaterial({
    map: moonTexture,
    normalMap: normalTexture,
  })
);
scene.add(moon);
moon.position.z = 10;
moon.position.setX(15);
moon.position.setY(0);

// DXC
const dxcTexture = new THREE.TextureLoader().load('dxc.jpg');
const dxc = new THREE.Mesh(new THREE.BoxGeometry(8, 8, 8), new THREE.MeshBasicMaterial({ map: dxcTexture }));
scene.add(dxc);
dxc.position.z = 25;
dxc.position.setX(60);
dxc.position.setY(-30);

// Geeks
const geeksTexture = new THREE.TextureLoader().load('geeks.jpg');
const geeks = new THREE.Mesh(new THREE.BoxGeometry(6, 6, 6), new THREE.MeshBasicMaterial({ map: geeksTexture }));
scene.add(geeks);
geeks.position.z = 12;
geeks.position.setX(-22);
geeks.position.setY(-18);

// canada flag
const canTexture = new THREE.TextureLoader().load('can-flag.jpg');
const canflag = new THREE.Mesh(
  new THREE.SphereGeometry(3, 32, 32),
  new THREE.MeshStandardMaterial({
    map: canTexture,
    normalMap: normalTexture,
  })
);
scene.add(canflag);
canflag.position.z = 10;
canflag.position.setX(-22);
canflag.position.setY(4);

// Loyalist Shape
const loyalTexture = new THREE.TextureLoader().load('Loyalist.jpg');
const Loyalist = new THREE.Mesh(new THREE.BoxGeometry(12, 6, 6), new THREE.MeshBasicMaterial({ map: loyalTexture }));
scene.add(Loyalist);
Loyalist.position.z = 40;
Loyalist.position.setX(15);
Loyalist.position.setY(-52);

// GGSIPU Shape
const ggsipuTexture = new THREE.TextureLoader().load('ggsipu.jpg');
const Ggsipu = new THREE.Mesh(new THREE.BoxGeometry(15, 6, 6), new THREE.MeshBasicMaterial({ map: ggsipuTexture }));
scene.add(Ggsipu);
Ggsipu.position.z = 40;
Ggsipu.position.setX(15);
Ggsipu.position.setY(-58.5);

// Udemy Shape
const udemyTexture = new THREE.TextureLoader().load('udemy.jpg');
const Udemy = new THREE.Mesh(new THREE.BoxGeometry(18, 6, 6), new THREE.MeshBasicMaterial({ map: udemyTexture }));
scene.add(Udemy);
Udemy.position.z = 40;
Udemy.position.setX(15);
Udemy.position.setY(-65);

scene.background = new THREE.Color( 0xF1F3F9);

window.addEventListener("resize", function(){
  var width = window.innerWidth;
  var height = window.innerHeight;
  renderer.setSize(width, height);
  camera.aspect = width/height;
  camera.updateProjectionMatrix();
});

var lastScrollTop = 0;
window.addEventListener("scroll", function(){
   var st = window.pageYOffset || document.documentElement.scrollTop;  
   var screenPos = (window.pageYOffset)/(document.documentElement.scrollHeight - document.documentElement.clientHeight);
   console.log("Scrollposition: ",screenPos)
   if (st > lastScrollTop){
    // camera.position.x += 0.5; 
    // camera.position.y = -80*screenPos; 
    // camera.position.z += 0.5; 
    camera.position.x = 98.5*screenPos;
    camera.position.y = -80*screenPos;
    camera.position.z = 25+(88.5*screenPos);
    console.log("X : "+camera.position.x);
    console.log("Y : "+camera.position.y);
    console.log("Z : "+camera.position.z);
   } else {
    // camera.position.x -= 0.5; 
    // camera.position.y = -80*screenPos; 
    // camera.position.z -= 0.5; 
    camera.position.x = 98.5*screenPos;
    camera.position.y = -80*screenPos;
    camera.position.z = 25+(88.5*screenPos);

    console.log("X : "+camera.position.x);
    console.log("Y : "+camera.position.y);
    console.log("Z : "+camera.position.z);
   }
   lastScrollTop = st <= 0 ? 0 : st; 
}, false);

function animate() {
    requestAnimationFrame(animate);
    torus.rotation.x += 0.01;
    torus.rotation.y += 0.005;
    torus.rotation.z += 0.01;
    
    dxc.rotation.y += 0.005;
    moon.rotation.y += 0.005;
    canflag.rotation.y += 0.015;

    renderer.render(scene, camera);
  }
  
  animate();
